<template>
    <div class="time-picker">
        <div class="back">
            <div class="icon"><i class="iconfont icon-fanhui" @click="_close"></i></div>
            <div class="title">选择取货时间</div>
            <div></div>
        </div>
        <div class="times">
            <cube-scroll>
                <div class="block date">
                    <div class="label">选择日期</div>
                    <cube-radio-group v-model="dateSelected" @input="_chose" :options="dateOptions" :col-num="colNum" />
                </div>
                <div class="block time">
                    <div class="label">选择时间</div>
                    <cube-radio-group v-model="timeSelected" @input="_chose" :options="timeOptions" :col-num="colNum" />
                </div>
            </cube-scroll>
        </div>
        <div class="btns" :class="{'m-disabled-btn': disabled}" @click="_sure">
            确定
        </div>
    </div>
</template>
<script>
import { queryAppointmentTime } from "@/api/order.js";
import { dateFormat } from "@/utils/util.js";
const tomorrow = (new Date()).setTime((new Date()).getTime()+24*60*60*1000);
console.log('tomorrow', tomorrow)
export default {
    data() {
        return {
            tomorrow: dateFormat(new Date(tomorrow), 'yyyy-MM-dd'),
            disabled: true,
            dateSelected: '',
            dateOptions: [],
            colNum: 2,
            timeSelected: '',
            timeOptions: [],
            allDate: [],
        }
    },
    created() {
        this._queryAppointmentTime();
    },
    watch: {
        dateSelected() {
            this.timeSelected = '';
            this.timeOptions = [];
            this.allDate[this.dateSelected].timeList.forEach((item, index) => {
                this.timeOptions.push({
                    label: item.appointmentTimeFrame,
                    value: item.id,
                    disabled: item.orderNumNow === item.orderNumSum,
                })
            })            
        },
    },
    methods: {
        _chose() {
            if (this.dateSelected !== '' && this.timeSelected !== '') {
                this.disabled = false;
            } else {
                this.disabled = true;
            }
        },
        _close() {
            this.$emit('close')
        },
        _sure() {
            let time = `${this.dateOptions[this.dateSelected].label} ${this.timeOptions.find(item => item.value === this.timeSelected).label}`
            let id = this.timeSelected;
            if (!this.disabled) {
                this.$emit('close', time, id)
            }
        },
        _queryAppointmentTime() {
            let params = {
                appointmentDate : this.tomorrow,
                id: '',
            }
            queryAppointmentTime(params).then(res => {
                if (!res.length) return;
                this.allDate = res;
                res.forEach((item, index) => {
                    this.dateOptions.push({
                        label: item.appointmentDate,
                        value: index,
                    })
                })
                res[0].timeList.forEach((item, index) => {
                    this.timeOptions.push({
                        label: item.appointmentTimeFrame,
                        value: index,
                        disabled: item.orderNumNow === item.orderNumSum,
                    })
                })
            })
        },
    }
}
</script>
<style lang="stylus" scoped>
.time-picker {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ffffff;
    z-index: 999;
    .back {
        height: 98px;
        line-height: 98px;
        display: flex;
        border-bottom: 1px solid #F2F2F2;
        div {
            flex: 1;
        }
        .icon {
            padding-left: 24px;
            i {
                font-size: 36px;
            }
        }
        .title {
            text-align: center;
        }
    }
    .times {
        height: calc(100vh - 196px);
        font-family:HiraginoSansGB-W3;
        padding: 24px;
        .block {
            margin-bottom: 24px;
            .label {
                margin-bottom: 24px;
                font-size: 28px;
            }
            .items, .cube-radio-group {
                display: flex;
                flex-direction: row;
                 flex-wrap: wrap;
                .item, .cube-radio {
                    width: 50%;
                    text-align: left;
                    font-size: 28px;
                }
            }
        }
    }
    .btns {
        height: 98px;
        line-height: 98px;
        background-color: #4A4C59;
        text-align: center;
        color: #ffffff;
    }
}
</style>


